import accounting from 'accounting';
import { getLocale } from '~global-js/helpers/locale';
import currencySymbolMap from '~global/variables/currency.json';

export const defaultCurrencyCode = 'USD';

export const currencies = [
  { code: 'AED', label: 'AED (د.إ)' },
  { code: 'AFN', label: 'AFN (؋)' },
  { code: 'AUD', label: 'AUD ($)' },
  { code: 'CAD', label: 'CAD ($)' },
  { code: 'CNY', label: 'CNY (¥)' },
  { code: 'EUR', label: 'EUR (€)' },
  { code: 'GBP', label: 'GBP (£)' },
  { code: 'HKD', label: 'HKD ($)' },
  { code: 'JPY', label: 'JPY (¥)' },
  { code: 'KRW', label: 'KRW (₩)' },
  { code: 'MXN', label: 'MXN ($)' },
  { code: 'NZD', label: 'NZD ($)' },
  { code: 'PLN', label: 'PLN (zł)' },
  { code: 'USD', label: 'USD ($)' },
  { code: 'ZAR', label: 'ZAR (R)' },
];

export const localeToCurrencyMapping = {
  'en-gb': 'GBP',
  'en-us': 'USD',
  'zh-cn': 'CNY',
  'zh-tw': 'USD',
  'zh-hk': 'HKD',
  'ko-kr': 'KRW',
  'ja-jp': 'JPY',
  'de-de': 'EUR',
  'el-gr': 'EUR',
  'el-cy': 'EUR',
  'es-es': 'EUR',
  'es-la': 'MXN',
  'fr-fr': 'EUR',
  'it-it': 'EUR',
};

export function getSymbolFromCurrency(currencyCode) {
  if (typeof currencyCode !== 'string') {
    return null;
  }

  return currencySymbolMap[currencyCode.toUpperCase()] || null;
}

export function getDefaultCurrencyCode(locale) {
  if (localeToCurrencyMapping[locale]) {
    return localeToCurrencyMapping[locale];
  }

  return defaultCurrencyCode;
}

export function getDefaultPaymentFrequency() {
  return 'month'; // TODO should this come from platform as needs to be same format they use
}

export function formatCurrency(i18n, amount, currencyCode, precision = 0) {
  if (!amount) {
    return null;
  }

  const locale = getLocale();
  let thousandSymbol = i18n.t('thousand_separator.currency.thousand_symbol');

  if ((locale === 'es-es' || locale === 'es-la') && amount < 10000) {
    thousandSymbol = '';
  }

  return accounting.formatMoney(
    amount,
    {
      symbol: getSymbolFromCurrency(currencyCode),
      precision,
      thousand: thousandSymbol,
      decimal: i18n.t('decimal_separator.currency.decimal_symbol'),
      format: i18n.t('global.currency.symbol_value_order', {
        interpolation: {
          prefix: '{{',
          suffix: '}}',
        },
      }),
    }
  );
}



